<template>
  <nut-popup v-model:visible="isListVisible"
             closeable
             :style="{ padding: '30px 10px',height:'85%',width:'90%',overflow:'auto' }">
    <nut-row>
      <nut-col :span="24">
        <div class="content title">{{title}}</div>
      </nut-col>
    </nut-row>
    <HadList ref="hadRef"></HadList>
    <HHadList ref="hhadRef"></HHadList>
    <div>比分胜平负推荐 首选：{{crsRecommend.firstOption}} 次选：{{crsRecommend.secondOption}}</div>
    <div>半全胜平负推荐 首选：{{hafuRecommend.firstOption}} 次选：{{hafuRecommend.secondOption}}</div>
  </nut-popup>
</template>

<script setup>
import { ref } from 'vue';
import { showNotify } from '@nutui/nutui'
import axios from 'axios'
import HHadList from './HHadList.vue';
import HadList from './HadList.vue';
import TtgConvert from './TtgConvert.vue';
import CrsConvert from './CrsConvert.vue';
import { getCrsRecommend, getHafuRecommend, PoissonCalculation } from './calculation.js'
const timeNotify = (msg) => {
  showNotify.warn(msg, { duration: 2000 })
}
const title = ref('');
const isListVisible = ref(false);
// 显示弹出框
const show = (matchId) => {
  isListVisible.value = true;
  getFixedBonusV1(matchId);
}

const oddsHistory = ref({});
const hadRef = ref(null);
const hhadRef = ref(null);
const crsRecommend = ref({});
const hafuRecommend = ref({});
const bosong = ref({});
const getFixedBonusV1 = async (matchId) => {
  try {
    const response = await axios.get('https://webapi.sporttery.cn/gateway/jc/football/getFixedBonusV1.qry?clientCode=3001&matchId=' + matchId);
    let data = response.data;
    if (data.success) {
      oddsHistory.value = data.value.oddsHistory;
      title.value = oddsHistory.value.homeTeamAbbName + ' VS ' + oddsHistory.value.awayTeamAbbName
      hadRef.value.pushHadList(oddsHistory.value.hadList);
      hhadRef.value.pushHHadList(oddsHistory.value.hhadList);
      crsRecommend.value = getCrsRecommend(oddsHistory.value.crsList);
      hafuRecommend.value = getHafuRecommend(oddsHistory.value.hafuList);
    } else {
      timeNotify(data.errorMessage);
    }
  } catch (error) {
    // 处理错误
    console.error(error);
  }
}

// 关闭弹出框
const close = () => {
  isVisible.value = false;
}

// 暴露给外部使用的函数
defineExpose({
  show,
  close
});

</script>

<style>
.fontCoarse {
  font-weight: bold;
}
.title {
  line-height: 30px !important;
  border: 0px !important;
}
.content {
  line-height: 45px;
  text-align: center;
  border: 1px solid var(--nut-table-border-color, #ececec);
  vertical-align: middle;
}
.left {
  line-height: 45px;
  text-align: left;
  border: 1px solid var(--nut-table-border-color, #ececec);
  padding-left: var(--nut-table-cols-padding, 5px);
}
</style>