<template>
  <nut-popup v-model:visible="isListVisible"
             closeable
             :style="{ padding: '30px 10px',height:'85%',width:'90%',overflow:'auto' }">
    <nut-row>
      <nut-col :span="24">
        <div class="content title">{{title}}</div>
      </nut-col>
    </nut-row>
    <TtgConvert ref="ttgRef"></TtgConvert>
    <CrsConvert ref="crsRef"></CrsConvert>
  </nut-popup>
</template>

<script setup>
import { ref } from 'vue';
import { showNotify } from '@nutui/nutui'
import axios from 'axios'
import TtgConvert from './TtgConvert.vue';
import CrsConvert from './CrsConvert.vue';
const timeNotify = (msg) => {
  showNotify.warn(msg, { duration: 2000 })
}
const title = ref('');
const isListVisible = ref(false);
// 显示弹出框
const show = (matchId) => {
  isListVisible.value = true;
  getFixedBonusV1(matchId);
}
const oddsHistory = ref({});
const ttgRef = ref(null);
const crsRef = ref(null);
const getFixedBonusV1 = async (matchId) => {
  try {
    const response = await axios.get('https://webapi.sporttery.cn/gateway/jc/football/getFixedBonusV1.qry?clientCode=3001&matchId=' + matchId);
    let data = response.data;
    if (data.success) {
      oddsHistory.value = data.value.oddsHistory;
      title.value = oddsHistory.value.homeTeamAbbName + ' VS ' + oddsHistory.value.awayTeamAbbName
      ttgRef.value.pushTtgList(oddsHistory.value.ttgList);
      crsRef.value.pushCrsList(oddsHistory.value.crsList);
    } else {
      timeNotify(data.errorMessage);
    }
  } catch (error) {
    // 处理错误
    console.error(error);
  }
}

// 关闭弹出框
const close = () => {
  isVisible.value = false;
}

// 暴露给外部使用的函数
defineExpose({
  show,
  close
});

</script>

<style>
.fontCoarse {
  font-weight: bold;
}
.title {
  line-height: 30px !important;
  border: 0px !important;
}
.content {
  line-height: 45px;
  text-align: center;
  border: 1px solid var(--nut-table-border-color, #ececec);
  vertical-align: middle;
}
.left {
  line-height: 45px;
  text-align: left;
  border: 1px solid var(--nut-table-border-color, #ececec);
  padding-left: var(--nut-table-cols-padding, 5px);
}
</style>