<template>
  <div class="boayClass">
    <div class="content">
      <div class="title">注册</div>
      <form @submit.prevent="handleSubmit">
        <label for="username">账户:</label>
        <input type="text"
               placeholder="请输入账号"
               v-model="loginForm.username"
               required
               maxlength="30">
        <label for="username">密码:</label>
        <input type="password"
               placeholder="请输入密码"
               v-model="loginForm.password"
               required
               maxlength="20">
        <label for="username">密码:</label>
        <input type="password"
               placeholder="请输入重复密码"
               v-model="loginForm.confirmPassword"
               required
               maxlength="20">
        <label for="username">验证码:</label>
        <input type="text"
               placeholder="验证码"
               v-model="loginForm.code"
               maxlength="4"
               :style="{width:'100px'}"
               required>
        <div class="login-code">
          <img :src="codeUrl"
               @click="getCode"
               class="login-code-img" />
        </div>
        <button class="btn">注册</button>
      </form>
      <span>已有账号<text @click="clickLogin">登录</text></span>
    </div>
  </div>
</template>

<script setup>
import { showNotify } from '@nutui/nutui'
import request from '@/utils/request';
import service from '@/utils/request';
import { useRouter } from 'vue-router';
import { onMounted, ref } from 'vue';
const timeNotify = (msg) => {
  showNotify.warn(msg, { duration: 2000 })
}
const successNotify = (msg) => {
  showNotify.success(msg)
}
const router = useRouter();
const codeUrl = ref("");
const captchaEnabled = ref(true);
const loginForm = ref({
  username: "",
  password: "",
  confirmPassword: "",
  code: "",
  uuid: ''
})

const clickLogin = () => {
  router.push('/login');
}

const getCode = () => {
  request.get('/captchaImage').then(res => {
    captchaEnabled.value = res.captchaEnabled === undefined ? true : res.captchaEnabled
    if (captchaEnabled.value) {
      codeUrl.value = 'data:image/gif;base64,' + res.img
      loginForm.value.uuid = res.uuid
    }
  })
}

const handleSubmit = async () => {
  try {
    if (loginForm.value.username === "") {
      timeNotify("请输入您的账号")
    } else if (loginForm.value.password === "") {
      timeNotify("请输入您的密码")
    } else if (loginForm.value.confirmPassword === "") {
      timeNotify("请再次输入您的密码")
    } else if (loginForm.value.password !== loginForm.value.confirmPassword) {
      timeNotify("两次输入的密码不一致")
    } else if (loginForm.value.code === "" && captchaEnabled.value) {
      timeNotify("请输入验证码")
    } else {
      // 发送登录请求
      const response = await service.post('/register', {
        username: loginForm.value.username,
        password: loginForm.value.password,
        confirmPassword: loginForm.value.confirmPassword,
        code: loginForm.value.code,
        uuid: loginForm.value.uuid
      });
      if (response.code == 200) {
        // 假设响应中包含一个token字段，我们将其保存到localStorage中
        successNotify("注册成功");
      } else {
        timeNotify(response.msg);
        getCode();
        loginForm.value.code = '';
      }
    }
  } catch (error) {
    // 处理错误，比如显示错误信息
    console.error('Login failed:', error);

  }
}
onMounted(getCode);
</script>

<style scoped>
.boayClass {
  height: 100vh;
  width: 100vw;
  background: rgb(34, 193, 195);
  background: linear-gradient(
    4deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(253, 187, 45, 1) 100%
  );
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  text-align: center;
}
.content {
  height: 700px;
  width: 350px;
  border: 1px solid #ddd;
  background-color: #efefef;
  opacity: 0.9;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 5px 20px rgba(255, 255, 255, 0.7);
}

.content .title {
  font-size: 35px;
  color: rgb(193, 208, 93);
  font-weight: 600;
  /* margin-bottom: 20%; */
}

.content input {
  width: 250px;
  padding: 10px;
  border: 0;
  border-bottom: 1px solid #ddd;
  background-color: transparent;
  margin: 20px 0;
  font-size: 18px;
  outline: none;
}

.content label {
  width: 100px;
  padding: 10px;
  margin: 20px 0;
  font-size: 18px;
  outline: none;
}

.content input::placeholder {
  color: #090909;
}

.content .btn {
  width: 300px;
  height: 40px;
  border: 0;
  background: linear-gradient(
    200deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(253, 187, 45, 1) 100%
  );
  margin-top: 5%;
  border-radius: 5px;
  color: #efefef;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.content .btn:hover {
  background: linear-gradient(
    -200deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(253, 187, 45, 1) 100%
  );
  background-position-x: -300px;
}

.content span,
.content text {
  text-decoration: none;
  margin-top: 30px;
}
.content text {
  color: rgba(34, 193, 195, 0.5);
  font-size: 20px;
  cursor: pointer;
  margin-left: 5px;
}
.login-code {
  height: 38px;
  float: right;
  width: 150px;
}
.login-code-img {
  height: 38px;
  /* position: absolute; */
  /* margin-left: 10px;
  width: 200px; */
  margin-top: 20px;
}
</style>