<template>
  <nut-divider content-position="left">({{goalLine}})让球胜平负 首推：{{firstOption}}，次推：{{secondOption}}</nut-divider>
  <nut-row>
    <nut-col :span="9">
      <div class="left fontCoarse">时间</div>
    </nut-col>
    <nut-col :span="5">
      <div class="content fontCoarse"><span class="u-batNum">让胜</span><span class="u-img"></span>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content fontCoarse"><span class="u-batNum">让平</span><span class="u-img"></span>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content fontCoarse"><span class="u-batNum">让负</span><span class="u-img"></span>
      </div>
    </nut-col>
  </nut-row>
  <nut-row v-for="(change, index) in hhadList"
           :key="index">
    <nut-col :span="9">
      <div class="left fontCoarse">{{change.updateTime}}</div>
    </nut-col>
    <nut-col :span="5">
      <div class="content"><span class="u-batNum">{{change.h}}</span>
        <template v-if="change.hf == 1">
          <span class="u-img"><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png"><!--v-if--></span>
        </template>
        <template v-else-if="change.hf == -1">
          <span class="u-img"><!--v-if--><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png"></span>
        </template>
        <template v-else>
          <span class="u-img"></span>
        </template>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content"><span class="u-batNum">{{change.d}}</span>
        <template v-if="change.df == 1">
          <span class="u-img"><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png"><!--v-if--></span>
        </template>
        <template v-else-if="change.df == -1">
          <span class="u-img"><!--v-if--><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png"></span>
        </template>
        <template v-else>
          <span class="u-img"></span>
        </template>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content"><span class="u-batNum">{{change.a}}</span>
        <template v-if="change.af == 1">
          <span class="u-img"><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png"><!--v-if--></span>
        </template>
        <template v-else-if="change.af == -1">
          <span class="u-img"><!--v-if--><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png"></span>
        </template>
        <template v-else>
          <span class="u-img"></span>
        </template>
      </div>
    </nut-col>
  </nut-row>
</template>

<script setup>
import { ref } from 'vue';
const hhadList = ref([]);
const goalLine = ref('-1');
const firstOption = ref('无');
const secondOption = ref('无');
const pushHHadList = (list) => {
  if (list.length > 0) {
    for (let i = 0; i < list.length; i++) {
      goalLine.value = list[i].goalLine;
      let s = list[i].updateDate.split('-');
      list[i].date = s[1] + '-' + s[2];
    }
  }
  hhadList.value = list;
  let hL = [];
  let dL = [];
  let aL = [];
  for (let i = 0; i < list.length; i++) {
    hL[i] = parseFloat(list[i].h);
    dL[i] = parseFloat(list[i].d);
    aL[i] = parseFloat(list[i].a);
  }
  let hLS = computeDiscrepancy(hL);
  let dLS = computeDiscrepancy(dL);
  let aLs = computeDiscrepancy(aL);
  let numbers = [hLS, dLS, aLs];
  numbers = numbers.slice().sort((a, b) => a - b);
  if (list.length > 1) {
    if (numbers[0] == hLS) {
      firstOption.value = '让胜';
    } else if (numbers[0] == dLS) {
      firstOption.value = '让平';
    } else {
      firstOption.value = '让负';
    }
    if (numbers[1] == hLS) {
      secondOption.value = '让胜';
    } else if (numbers[1] == dLS) {
      secondOption.value = '让平';
    } else {
      secondOption.value = '让负';
    }
  }
}

const computeDiscrepancy = (array) => {
  if (!Array.isArray(array) || array.length === 0) {
    return 0;
  }
  const mean = array.reduce((sum, value) => sum + value, 0) / array.length;
  return array.reduce((discrepancy, value) => {
    return discrepancy + Math.abs(value - mean);
  }, 0);
}
// 暴露给外部使用的函数
defineExpose({
  pushHHadList
});
</script>

<style scoped>
.u-img {
  width: 11px;
  display: inline-block;
}
.u-batNum {
  padding: 0 2px;
}
</style>