<template>
  <nut-divider content-position="left">半全场胜平负</nut-divider>
  <nut-row v-for="(change, index) in changeList"
           :key="index">
    <nut-col :span="8">
      <nut-row>
        <nut-col :span="24">
          <div class="left fontCoarse"
               style="height: 92px;align-items:center;display: flex;justify-content: center; /* 水平居中 */">{{change.name}}</div>
        </nut-col>
      </nut-row>
      <nut-row>
        <nut-col :span="24">
          <div class="content fontCoarse"><span class="u-batNum">平平</span><span class="u-img"><img alt=""></span>
          </div>
        </nut-col>
      </nut-row>
      <nut-row>
        <nut-col :span="24">
          <div class="content"><span class="u-batNum">{{change.dd}}</span><span class="u-img"><img alt=""
                   :src="change.ddf"></span>
          </div>
        </nut-col>
      </nut-row>

    </nut-col>
    <nut-col :span="16">
      <nut-row>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">胜胜</span><span class="u-img"><img alt=""></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">胜平</span><span class="u-img"><img alt=""></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">胜负</span><span class="u-img"><img alt=""></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">平胜</span><span class="u-img"><img alt=""></span>
          </div>
        </nut-col>
      </nut-row>
      <nut-row>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.hh}}</span><span class="u-img"><img alt=""
                   :src="change.hhf"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.hd}}</span><span class="u-img"><img alt=""
                   :src="change.hdf"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.ha}}</span><span class="u-img"><img alt=""
                   :src="change.haf"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.dh}}</span><span class="u-img"><img alt=""
                   :src="change.dhf"></span>
          </div>
        </nut-col>
      </nut-row>
      <nut-row>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">平负</span><span class="u-img"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">负胜</span><span class="u-img"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">负平</span><span class="u-img"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">负负</span><span class="u-img"></span>
          </div>
        </nut-col>
      </nut-row>
      <nut-row>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.da}}</span><span class="u-img"><img alt=""
                   :src="change.daf"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.ah}}</span><span class="u-img"><img alt=""
                   :src="change.ahf"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.ad}}</span><span class="u-img"><img alt=""
                   :src="change.adf"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.aa}}</span><span class="u-img"><img alt=""
                   :src="change.aaf"></span>
          </div>
        </nut-col>
      </nut-row>
    </nut-col>
  </nut-row>
</template>

<script setup>
import { ref } from 'vue';
const hafuList = ref([]);
const changeList = ref([]);
const pushHafuList = (list) => {
  hafuList.value = list;
  if (list.length > 0) {
    let up = "https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png";
    let down = "https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png";
    let initHufa = list[0];
    let lastHufa = list[list.length - 1];
    let change = [];
    let hhf = 0;
    let hdf = 0;
    let haf = 0;
    let dhf = 0;
    let ddf = 0;
    let daf = 0;
    let ahf = 0;
    let adf = 0;
    let aaf = 0;
    change.push({
      name: "初始:" + initHufa.updateTime, hh: initHufa.hh, hd: initHufa.hd, ha: initHufa.ha,
      dh: initHufa.dh, dd: initHufa.dd, da: initHufa.da,
      ah: initHufa.ah, ad: initHufa.ad, aa: initHufa.aa,
      hhf: hhf, hdf: hdf, haf: haf, dhf: dhf, ddf: ddf, daf: daf, ahf: ahf, adf: adf, aaf: aaf
    });
    hhf = parseFloat(initHufa.hh) > parseFloat(lastHufa.hh) ? down : parseFloat(initHufa.hh) < parseFloat(lastHufa.hh) ? up : '';
    hdf = parseFloat(initHufa.hd) > parseFloat(lastHufa.hd) ? down : parseFloat(initHufa.hd) < parseFloat(lastHufa.hd) ? up : '';
    haf = parseFloat(initHufa.ha) > parseFloat(lastHufa.ha) ? down : parseFloat(initHufa.ha) < parseFloat(lastHufa.ha) ? up : '';
    dhf = parseFloat(initHufa.dh) > parseFloat(lastHufa.dh) ? down : parseFloat(initHufa.dh) < parseFloat(lastHufa.dh) ? up : '';
    ddf = parseFloat(initHufa.dd) > parseFloat(lastHufa.dd) ? down : parseFloat(initHufa.dd) < parseFloat(lastHufa.dd) ? up : '';
    daf = parseFloat(initHufa.da) > parseFloat(lastHufa.da) ? down : parseFloat(initHufa.da) < parseFloat(lastHufa.da) ? up : '';
    ahf = parseFloat(initHufa.ah) > parseFloat(lastHufa.ah) ? down : parseFloat(initHufa.ah) < parseFloat(lastHufa.ah) ? up : '';
    adf = parseFloat(initHufa.ad) > parseFloat(lastHufa.ad) ? down : parseFloat(initHufa.ad) < parseFloat(lastHufa.ad) ? up : '';
    aaf = parseFloat(initHufa.aa) > parseFloat(lastHufa.aa) ? down : parseFloat(initHufa.aa) < parseFloat(lastHufa.aa) ? up : '';
    change.push({
      name: "最新:" + lastHufa.updateTime, hh: lastHufa.hh, hd: lastHufa.hd, ha: lastHufa.ha,
      dh: lastHufa.dh, dd: lastHufa.dd, da: lastHufa.da,
      ah: lastHufa.ah, ad: lastHufa.ad, aa: lastHufa.aa,
      hhf: hhf, hdf: hdf, haf: haf, dhf: dhf, ddf: ddf, daf: daf, ahf: ahf, adf: adf, aaf: aaf
    });
    changeList.value = change;
  }
}
// 暴露给外部使用的函数
defineExpose({
  pushHafuList
});
</script>

<style scoped>
.u-img {
  width: 11px;
  display: inline-block;
}
.u-batNum {
  padding: 0 2px;
}
</style>