<template>
  <nut-divider content-position="left">胜平负 首推：{{firstOption}}，次推：{{secondOption}}</nut-divider>
  <nut-row>
    <nut-col :span="9">
      <div class="left fontCoarse">时间</div>
    </nut-col>
    <nut-col :span="5">
      <div class="content fontCoarse"><span class="u-batNum">胜</span><span class="u-img"></span>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content fontCoarse"><span class="u-batNum">平</span><span class="u-img"></span>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content fontCoarse"><span class="u-batNum">负</span><span class="u-img"></span>
      </div>
    </nut-col>
  </nut-row>
  <nut-row v-for="(change, index) in hadList"
           :key="index">
    <nut-col :span="9">
      <div class="left fontCoarse">{{change.date}} {{change.updateTime}}</div>
    </nut-col>
    <nut-col :span="5">
      <div class="content"><span class="u-batNum">{{change.h}}</span>
        <template v-if="change.hf == 1">
          <span class="u-img"><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png"><!--v-if--></span>
        </template>
        <template v-else-if="change.hf == -1">
          <span class="u-img"><!--v-if--><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png"></span>
        </template>
        <template v-else>
          <span class="u-img"></span>
        </template>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content"><span class="u-batNum">{{change.d}}</span>
        <template v-if="change.df == 1">
          <span class="u-img"><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png"><!--v-if--></span>
        </template>
        <template v-else-if="change.df == -1">
          <span class="u-img"><!--v-if--><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png"></span>
        </template>
        <template v-else>
          <span class="u-img"></span>
        </template>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content"><span class="u-batNum">{{change.a}}</span>
        <template v-if="change.af == 1">
          <span class="u-img"><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png"><!--v-if--></span>
        </template>
        <template v-else-if="change.af == -1">
          <span class="u-img"><!--v-if--><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png"></span>
        </template>
        <template v-else>
          <span class="u-img"></span>
        </template>
      </div>
    </nut-col>
  </nut-row>
</template>

<script setup>
import { h, ref } from 'vue';
const hadList = ref([]);
const firstOption = ref('无');
const secondOption = ref('无');
const pushHadList = (list) => {
  if (list.length > 0) {
    for (let i = 0; i < list.length; i++) {
      let s = list[i].updateDate.split('-');
      list[i].date = s[1] + '-' + s[2];
    }
  }
  hadList.value = list;
  let hL = [];
  let dL = [];
  let aL = [];
  for (let i = 0; i < list.length; i++) {
    hL[i] = parseFloat(list[i].h);
    dL[i] = parseFloat(list[i].d);
    aL[i] = parseFloat(list[i].a);
  }
  let hLS = computeDiscrepancy(hL);
  let dLS = computeDiscrepancy(dL);
  let aLs = computeDiscrepancy(aL);
  let numbers = [hLS, dLS, aLs];
  numbers = numbers.slice().sort((a, b) => a - b);
  if (list.length > 1) {
    if (numbers[0] == hLS) {
      firstOption.value = '胜';
    } else if (numbers[0] == dLS) {
      firstOption.value = '平';
    } else {
      firstOption.value = '负';
    }
    if (numbers[1] == hLS) {
      secondOption.value = '胜';
    } else if (numbers[1] == dLS) {
      secondOption.value = '平';
    } else {
      secondOption.value = '负';
    }
  }
}

const probabilities = (array) => {
  let list = [];
  for (let i = 0; i < array.length; i++) {
    let hadSum = 0;
    let hadList = [];
    for (let j = 0; j < array[i].length; j++) {
      hadSum = count(hadSum, count(1, array[i][j], '/'), '+');
    }
    let hadRate = count(100, hadSum, '/')
    for (let j = 0; j < array[i].length; j++) {
      hadList[j] = count(hadRate, array[i][j], '/')
    }
    list[i] = hadList;
  }
  return list;
}

const computeDiscrepancy = (array) => {
  if (!Array.isArray(array) || array.length === 0) {
    return 0;
  }
  const mean = array.reduce((sum, value) => sum + value, 0) / array.length;
  return array.reduce((discrepancy, value) => {
    return discrepancy + Math.abs(value - mean);
  }, 0);
}

const count = (num1, num2, symbol) => {
  let str1 = num1.toString();
  let str2 = num2.toString();
  let result,
    len1,
    len2,
    step;
  try {
    len1 = str1.split('.')[1].length
  } catch (e) {
    len1 = 0
  };
  try {
    len2 = str2.split('.')[1].length
  } catch (e) {
    len2 = 0
  };
  step = Math.pow(10, Math.max(len1, len2));
  let a = parseInt(num1 * step);
  let b = parseInt(num2 * step);
  switch (symbol) {
    case '+':
      result = (a + b) / step
      break;
    case "-":
      result = (a - b) / step
      break;
    case "*":
      result = (a * b) / step / step
      break;
    case "/":
      result = a / b
      break;
    default:
      break;
  }
  return result;
}

// 暴露给外部使用的函数
defineExpose({
  pushHadList
});
</script>

<style scoped>
.u-img {
  width: 11px;
  display: inline-block;
}
.u-batNum {
  padding: 0 2px;
}
</style>