<template>
  <nut-divider content-position="left">总进球</nut-divider>
  <nut-row v-for="(change, index) in changeList"
           :key="index">
    <nut-col :span="8">
      <div class="left fontCoarse"
           style="height: 186px;align-items:center;display: flex;justify-content: center; /* 水平居中 */">{{change.name}}</div>
    </nut-col>
    <nut-col :span="16">
      <nut-row>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">0球</span><span class="u-img"><img alt=""></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">1球</span><span class="u-img"><img alt=""></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">2球</span><span class="u-img"><img alt=""></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">3球</span><span class="u-img"><img alt=""></span>
          </div>
        </nut-col>
      </nut-row>
      <nut-row>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.s0}}</span><span class="u-img"><img alt=""
                   :src="change.s0f"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.s1}}</span><span class="u-img"><img alt=""
                   :src="change.s1f"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.s2}}</span><span class="u-img"><img alt=""
                   :src="change.s2f"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.s3}}</span><span class="u-img"><img alt=""
                   :src="change.s3f"></span>
          </div>
        </nut-col>
      </nut-row>
      <nut-row>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">4球</span><span class="u-img"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">5球</span><span class="u-img"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">6球</span><span class="u-img"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content fontCoarse"><span class="u-batNum">7球</span><span class="u-img"></span>
          </div>
        </nut-col>
      </nut-row>
      <nut-row>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.s4}}</span><span class="u-img"><img alt=""
                   :src="change.s4f"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.s5}}</span><span class="u-img"><img alt=""
                   :src="change.s5f"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.s6}}</span><span class="u-img"><img alt=""
                   :src="change.s6f"></span>
          </div>
        </nut-col>
        <nut-col :span="6">
          <div class="content"><span class="u-batNum">{{change.s7}}</span><span class="u-img"><img alt=""
                   :src="change.s7f"></span>
          </div>
        </nut-col>
      </nut-row>
    </nut-col>
  </nut-row>
</template>

<script setup>
import { ref } from 'vue';
const ttgList = ref([]);
const changeList = ref([]);
const pushTtgList = (list) => {
  ttgList.value = list;
  if (list.length > 0) {
    let up = "https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png";
    let down = "https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png";
    let initTtg = list[0];
    let lastTtg = list[list.length - 1];
    let change = [];
    let s0f = 0;
    let s1f = 0;
    let s2f = 0;
    let s3f = 0;
    let s4f = 0;
    let s5f = 0;
    let s6f = 0;
    let s7f = 0;
    change.push({
      name: "初始:" + initTtg.updateTime, s0: initTtg.s0, s1: initTtg.s1, s2: initTtg.s2,
      s3: initTtg.s3, s4: initTtg.s4, s5: initTtg.s5,
      s6: initTtg.s6, s7: initTtg.s7,
      s0f: s0f, s1f: s1f, s2f: s2f, s3f: s3f, s4f: s4f, s5f: s5f, s6f: s6f, s7f: s7f
    });
    s0f = parseFloat(initTtg.s0) > parseFloat(lastTtg.s0) ? down : parseFloat(initTtg.s0) < parseFloat(lastTtg.s0) ? up : '';
    s1f = parseFloat(initTtg.s1) > parseFloat(lastTtg.s1) ? down : parseFloat(initTtg.s1) < parseFloat(lastTtg.s1) ? up : '';
    s2f = parseFloat(initTtg.s2) > parseFloat(lastTtg.s2) ? down : parseFloat(initTtg.s2) < parseFloat(lastTtg.s2) ? up : '';
    s3f = parseFloat(initTtg.s3) > parseFloat(lastTtg.s3) ? down : parseFloat(initTtg.s3) < parseFloat(lastTtg.s3) ? up : '';
    s4f = parseFloat(initTtg.s4) > parseFloat(lastTtg.s4) ? down : parseFloat(initTtg.s4) < parseFloat(lastTtg.s4) ? up : '';
    s5f = parseFloat(initTtg.s5) > parseFloat(lastTtg.s5) ? down : parseFloat(initTtg.s5) < parseFloat(lastTtg.s5) ? up : '';
    s6f = parseFloat(initTtg.s6) > parseFloat(lastTtg.s6) ? down : parseFloat(initTtg.s6) < parseFloat(lastTtg.s6) ? up : '';
    s7f = parseFloat(initTtg.s7) > parseFloat(lastTtg.s7) ? down : parseFloat(initTtg.s7) < parseFloat(lastTtg.s7) ? up : '';
    change.push({
      name: "最新:" + lastTtg.updateTime, s0: lastTtg.s0, s1: lastTtg.s1, s2: lastTtg.s2,
      s3: lastTtg.s3, s4: lastTtg.s4, s5: lastTtg.s5,
      s6: lastTtg.s6, s7: lastTtg.s7,
      s0f: s0f, s1f: s1f, s2f: s2f, s3f: s3f, s4f: s4f, s5f: s5f, s6f: s6f, s7f: s7f
    });
    changeList.value = change;
  }
}
// 暴露给外部使用的函数
defineExpose({
  pushTtgList
});
</script>

<style scoped>
.u-img {
  width: 11px;
  display: inline-block;
}
.u-batNum {
  padding: 0 2px;
}
</style>