          </div>
          <div class="u-btm">{{matchHead.seasonName  }} {{matchHead.phaseName  }} <template v-if="matchHead.gameweek!=''"> 第{{matchHead.gameweek}}轮</template> {{ matchHead.matchDateTime }}</div>
        </div>
      </div>
    </nut-col>
  </nut-row>
  <div class="m-box">
