          <p>赛季主场成绩: <span>{{ wbsjStatsHome.sHomeWinGoalMatchCnt }}</span>胜 <span>{{ wbsjStatsHome.sHomeDrawMatchCnt }}</span>平 <span>{{wbsjStatsHome.sHomeLossGoalMatchCnt }}</span>负</p>
        </div>
      </nut-col>
      <nut-col :span="12">
        <div class="u-detail">
          <p>排名: 常规赛 第<span>{{wbsjStatsAway.ranking }}</span>名 </p>
          <p>赛季总成绩: <span>{{ wbsjStatsAway.sWinGoalMatchCnt }}</span>胜 <span>{{wbsjStatsAway.sDrawMatchCnt }}</span>平 <span>{{ wbsjStatsAway.sLossGoalMatchCnt }}</span>负</p>
          <p>赛季客场成绩: <span>{{ wbsjStatsAway.sHomeWinGoalMatchCnt }}</span>胜 <span>{{ wbsjStatsAway.sHomeDrawMatchCnt }}</span>平 <span>{{ wbsjStatsAway.sHomeLossGoalMatchCnt }}</span>负</p>
        </div>
      </nut-col>
    </nut-row>
  </div>
  <div class="m-title"><em></em>历史交锋
    <div class="m-titleRt">
      <div class="m-checkList"
           style="opacity: 1;">
        <div class="u-check"
             :class="{active:isActive1 }"
             @click="clickDiv({flag:0,isActive:1}, $event)"><span>全部赛制</span></div>
        <div class="u-check"
             :class="{active:isActive2 }"
             @click="clickDiv({flag:1,isActive:2}, $event)">相同赛制</div>
      </div>
      <div class="m-checkList"
           style="opacity: 1;">
        <div class="u-check"
             :class="{active:isActive3 }"
             @click="clickDiv({flag:0,isActive:3}, $event)">不区分主客场</div>
        <div class="u-check"
             :class="{active:isActive4 }"
             @click="clickDiv({flag:1,isActive:4}, $event)">相同主客场</div>
      </div>
    </div>
    <div class="m-titleRt">
      <div class="m-checkList"
           style="opacity: 1;margin-left: 280px;">
        <div class="u-check"
             :class="{active:isActive5 }"
             @click="clickDiv({flag:0,isActive:5}, $event)">近10场</div>
        <div class="u-check"
             :class="{active:isActive6 }"
             @click="clickDiv({flag:1,isActive:6}, $event)">近20场</div>
      </div>
    </div>
  </div>
  <div class="m-tableBox">
    <div class="m-tableTitle"><span class="u-tit">近{{historyStatistics.totalLegCnt}}场</span><span class="u-team">{{ historyStatistics.teamShortName }}</span><span class="u-red">{{historyStatistics.winGoalMatchCnt}}胜 ({{ historyStatistics.winProbability }})</span> | <span class="u-green">{{historyStatistics.drawMatchCnt}}平 ({{ historyStatistics.drawProbability }})</span> | <span class="u-blue">{{historyStatistics.lossGoalMatchCnt}}负 ({{historyStatistics.lossProbability}})</span></div>
    <table class="m-tableData">
