        </th>
      </tr>
      <tr v-for="(match, key) in historyMatchList"
          :key="key">
        <td class="u-date">{{ match.matchDate }}</td>
        <td>{{  match.tournamentShortName}}</td>
        <td class="u-team"><a>
            <div class="m-tableFlex">
              <div class="left1 hoverClass">{{ match.homeTeamShortName }}</div>
              <div class="center1">
                <p class="u-top">{{ match.fullCourtGoal }}</p>
                <p class="u-btm hoverClass">（半 {{match.halfTimeGoal}}）</p>
              </div>
              <div class="right1 hoverClass">{{ match.awayTeamShortName }}</div>
            </div>
          </a></td>
      </tr>
    </table>
  </div>
  <div class="m-title"><em></em>比赛近况
    <div class="m-titleRt">
      <div class="m-checkList"
           style="opacity: 1;">
        <div class="u-check"
             :class="{active:isActive7 }"
             @click="clickDiv3({flag:0,isActive:7}, $event)"><span>{{matchHead.homeTeamShortName}}</span></div>
        <div class="u-check"
             :class="{active:isActive8 }"
             @click="clickDiv3({flag:1,isActive:8}, $event)">{{matchHead.awayTeamShortName}}</div>
      </div>
      <div class="m-checkList"
           style="opacity: 1;">
        <div class="u-check"
             :class="{active:isActive9 }"
             @click="clickDiv2({flag:0,isActive:9}, $event)"><span>全部赛制</span></div>
        <div class="u-check"
             :class="{active:isActive10 }"
             @click="clickDiv2({flag:1,isActive:10}, $event)">相同赛制</div>
      </div>
    </div>
    <div class="m-titleRt">
      <div class="m-checkList"
           style="opacity: 1;">
        <div class="u-check"
             :class="{active:isActive11 }"
             @click="clickDiv2({flag:0,isActive:11}, $event)">不区分主客场</div>
        <div class="u-check"
             :class="{active:isActive12 }"
             @click="clickDiv2({flag:1,isActive:12}, $event)">相同主客场</div>
      </div>
      <div class="m-checkList"
           style="opacity: 1;">
        <div class="u-check"
             :class="{active:isActive13 }"
             @click="clickDiv2({flag:0,isActive:13}, $event)">近10场</div>
        <div class="u-check"
             :class="{active:isActive14 }"
             @click="clickDiv2({flag:1,isActive:14}, $event)">近20场</div>
      </div>
    </div>
  </div>
  <div class="m-tableBox">
    <div class="m-tableTitle"><span class="u-tit">近{{teamStatistics.totalLegCnt}}场</span><span class="u-team">{{ teamStatistics.teamShortName }}</span><span class="u-red">{{teamStatistics.winGoalMatchCnt}}胜 ({{ teamStatistics.winProbability }})</span> | <span class="u-green">{{teamStatistics.drawMatchCnt}}平 ({{ teamStatistics.drawProbability }})</span> | <span class="u-blue">{{teamStatistics.lossGoalMatchCnt}}负 ({{teamStatistics.lossProbability}})</span></div>
    <table class="m-tableData">
      <tr>
        <th width="110">比赛日期</th>
        <th width="108">赛制</th>
        <th width="300">
          <div class="m-tableFlex">
            <div class="left1">主队</div>
            <div class="center1">VS</div>
            <div class="right1">客队</div>
          </div>
        </th>
      </tr>
      <tr v-for="(match, key) in teamMatchList"
          :key="key">
        <td class="u-date">{{ match.matchDate }}</td>
        <td>{{  match.tournamentShortName}}</td>
        <td class="u-team"><a>
            <div class="m-tableFlex">
              <div class="left1 hoverClass">{{ match.homeTeamShortName }}</div>
              <div class="center1">
                <p class="u-top">{{ match.fullCourtGoal }}</p>
                <p class="u-btm hoverClass">（半 {{match.halfTimeGoal}}）</p>
              </div>
              <div class="right1 hoverClass">{{ match.awayTeamShortName }}</div>
            </div>
          </a></td>
      </tr>
    </table>
  </div>
</template>
<script setup>
import { onMounted, ref } from 'vue'
import { useRoute } from 'vue-router';
import axios from 'axios'
const route = useRoute();
onMounted(() => {
  getMatchHeadV1();
  getResultHistoryV1(termLimits.value, tournamentFlag.value, homeAwayFlag.value);
  getMatchResultV1(termLimits2.value, tournamentFlag2.value, homeAwayFlag2.value);
})
const termLimits = ref(10);
const tournamentFlag = ref(0);
const homeAwayFlag = ref(0);
const termLimits2 = ref(10);
const tournamentFlag2 = ref(0);
const homeAwayFlag2 = ref(0);
const team = ref(0);
const clickDiv = (obj, event) => {
  if (obj.isActive == 1) {
    isActive1.value = true;
    isActive2.value = false;
    tournamentFlag.value = 0;
  } else if (obj.isActive == 2) {
    isActive1.value = false;
    isActive2.value = true;
    tournamentFlag.value = 1;
  }
  if (obj.isActive == 3) {
    isActive3.value = true;
    isActive4.value = false;
    homeAwayFlag.value = 0;
  } else if (obj.isActive == 4) {
    isActive3.value = false;
    isActive4.value = true;
    homeAwayFlag.value = 1;
  }
  if (obj.isActive == 5) {
    isActive5.value = true;
    isActive6.value = false;
    termLimits.value = 10;
  } else if (obj.isActive == 6) {
    isActive5.value = false;
    isActive6.value = true;
    termLimits.value = 20;
  }
  getResultHistoryV1(termLimits.value, tournamentFlag.value, homeAwayFlag.value);
  //console.log('被点击的元素:', event.target);
  //console.log('自定义数据对象:', obj);
}
const clickDiv3 = (obj, event) => {
  if (obj.isActive == 7) {
    isActive7.value = true;
    isActive8.value = false;
    team.value = 0;
  } else if (obj.isActive == 8) {
    isActive7.value = false;
    isActive8.value = true;
    team.value = 1;
  }
  if (team.value == 0) {
    teamMatchList.value = matchResult.value.home.matchList;
    teamStatistics.value = matchResult.value.home.statistics;
  } else {
    teamMatchList.value = matchResult.value.away.matchList;
    teamStatistics.value = matchResult.value.away.statistics;
  }

}
const clickDiv2 = (obj, event) => {
  if (obj.isActive == 9) {
    isActive9.value = true;
    isActive10.value = false;
    tournamentFlag2.value = 0;
  } else if (obj.isActive == 10) {
    isActive9.value = false;
    isActive10.value = true;
    tournamentFlag2.value = 1;
  }
  if (obj.isActive == 11) {
    isActive11.value = true;
    isActive12.value = false;
    homeAwayFlag2.value = 0;
  } else if (obj.isActive == 12) {
    isActive11.value = false;
    isActive12.value = true;
    homeAwayFlag2.value = 1;
  }
  if (obj.isActive == 13) {
    isActive13.value = true;
    isActive14.value = false;
    termLimits2.value = 10;
  } else if (obj.isActive == 14) {
    isActive13.value = false;
    isActive14.value = true;
    termLimits2.value = 20;
  }
  getMatchResultV1(termLimits2.value, tournamentFlag2.value, homeAwayFlag2.value);

  //console.log('被点击的元素:', event.target);
  //console.log('自定义数据对象:', obj);
}
const isActive1 = ref(true);
const isActive2 = ref(false);
const isActive3 = ref(true);
const isActive4 = ref(false);
const isActive5 = ref(true);
const isActive6 = ref(false);
const isActive7 = ref(true);
const isActive8 = ref(false);
const isActive9 = ref(true);
const isActive10 = ref(false);
const isActive11 = ref(true);
const isActive12 = ref(false);
const isActive13 = ref(true);
const isActive14 = ref(false);
const matchId = route.params.matchId;
const matchHead = ref({});
const homeGoal = ref('');
const awayGoal = ref('');
const colon = ref('');
const wbsjStatsHome = ref({});
const wbsjStatsAway = ref({});
const historyMatchList = ref([]);
const historyStatistics = ref({});
const teamMatchList = ref([]);
const teamStatistics = ref({});
const matchResult = ref({});
const getMatchHeadV1 = async () => {
  const response = await axios.get('https://webapi.sporttery.cn/gateway/uniform/football/getMatchHeadV1.qry?source=web&sportteryMatchId=' + matchId);
  let data = response.data;
  matchHead.value = data.value;
  let fullCourtGoal = data.value.fullCourtGoal;
  wbsjStatsHome.value = data.value.wbsjStats.home;
  wbsjStatsAway.value = data.value.wbsjStats.away;
  if (fullCourtGoal != '') {
    homeGoal.value = fullCourtGoal.split(":")[0];
    awayGoal.value = fullCourtGoal.split(":")[1];
    colon.value = ':'
  }
}

const getMatchResultV1 = async (termLimits, tournamentFlag, homeAwayFlag) => {
  const response = await axios.get('https://webapi.sporttery.cn/gateway/uniform/football/getMatchResultV1.qry?sportteryMatchId=' + matchId + '&termLimits=' + termLimits + '&tournamentFlag=' + tournamentFlag + '&homeAwayFlag=' + homeAwayFlag);
  let data = response.data;
  matchResult.value = data.value;
  if (team.value == 0) {
    teamMatchList.value = data.value.home.matchList;
    teamStatistics.value = data.value.home.statistics;
  } else {
    teamMatchList.value = data.value.away.matchList;
    teamStatistics.value = data.value.away.statistics;
  }

}

const getResultHistoryV1 = async (termLimits, tournamentFlag, homeAwayFlag) => {
  const response = await axios.get('https://webapi.sporttery.cn/gateway/uniform/football/getResultHistoryV1.qry?sportteryMatchId=' + matchId + '&termLimits=' + termLimits + '&tournamentFlag=' + tournamentFlag + '&homeAwayFlag=' + homeAwayFlag);
  let data = response.data;
  historyMatchList.value = data.value.matchList;
  historyStatistics.value = data.value.statistics;
}
</script>
