<template>
  <nut-popup v-model:visible="isListVisible"
             closeable
             :style="{ padding: '30px 10px',height:'85%',width:'90%',overflow:'auto' }">
    <nut-row>
      <nut-col :span="24">
        <div class="content title">{{title}}</div>
      </nut-col>
    </nut-row>
    <nut-divider content-position="left">泊松计算</nut-divider>
    <nut-noticebar :text="text"
                   wrapable />
    <nut-row>
      <nut-col :span="8">
        <div style="padding: 10px 2px;">
          相同赛制：<nut-switch v-model="tournamentFlag"
                      @change="tournamentChange" /></div>
      </nut-col>
      <nut-col :span="8">
        <div style="padding: 10px 2px;">
          主客同场：<nut-switch v-model="homeAwayFlag"
                      @change="homeAwayFlagChange" /></div>
      </nut-col>
      <nut-col :span="8">
        <nut-input v-model="termLimits"
                   type="number"
                   @update:model-value="updateTermLimits">
          <template #left>
            近
          </template>
          <template #right>
            场
          </template>
        </nut-input>
      </nut-col>
    </nut-row>

    <nut-table :columns="columns"
               :data="bosong.totalGoals"></nut-table>
    <nut-divider content-position="left">推荐比分</nut-divider>
    <nut-row>
      <nut-col :span="8"
               v-for="(index,key) in bosong.score"
               :key="key">
        <div class="content fontCoarse">{{index}}
        </div>
      </nut-col>
    </nut-row>
  </nut-popup>
</template>

<script setup>
import { ref } from 'vue';
import { showNotify } from '@nutui/nutui'
import axios from 'axios'
import { PoissonCalculation } from './calculation.js'
const timeNotify = (msg) => {
  showNotify.warn(msg, { duration: 2000 })
}
const title = ref('');
const isListVisible = ref(false);
const tournamentFlag = ref(false);
const homeAwayFlag = ref(false);
const termLimits = ref(6);
const tournamentFlagInt = ref(0);
const homeAwayFlagInt = ref(0);
const id = ref('')
const updateTermLimits = (val) => {
  getFixedBonusV1(id.value);
}
const text = ref(
  '该方法为计算主客近6场战绩所得进球数概率和比分概率，不可完全依照提示。可根据机构盘口和体彩（胜平负，比分，进球数）来进行判断分析'
)
// 显示弹出框
const show = (matchId) => {
  isListVisible.value = true;
  getFixedBonusV1(matchId);
}

const columns = ref([
  {
    title: '进球数',
    key: 'goal'
  },
  {
    title: '概率',
    key: 'probability'
  }
])
const tournamentChange = (value) => {
  if (value) {
    tournamentFlagInt.value = 1;
  } else {
    tournamentFlagInt.value = 0;
  }
  getFixedBonusV1(id.value);
}
const homeAwayFlagChange = (value) => {
  if (value) {
    homeAwayFlagInt.value = 1;
  } else {
    homeAwayFlagInt.value = 0;
  }
  getFixedBonusV1(id.value);
}
const oddsHistory = ref({});
const bosong = ref({});
const getFixedBonusV1 = async (matchId) => {
  try {
    id.value = matchId;
    const response = await axios.get('https://webapi.sporttery.cn/gateway/jc/football/getFixedBonusV1.qry?clientCode=3001&matchId=' + matchId);
    let data = response.data;
    if (data.success) {
      oddsHistory.value = data.value.oddsHistory;
      title.value = oddsHistory.value.homeTeamAbbName + ' VS ' + oddsHistory.value.awayTeamAbbName
      const response2 = await axios.get('https://webapi.sporttery.cn/gateway/uniform/football/getMatchResultV1.qry?sportteryMatchId=' + matchId + '&termLimits=' + termLimits.value + '&tournamentFlag=' + tournamentFlagInt.value + '&homeAwayFlag=' + homeAwayFlagInt.value);
      let data2 = response2.data;
      let value2 = data2.value;
      if (data2.success) {
        bosong.value = PoissonCalculation(
          value2.home.statistics.goalCnt,
          value2.home.statistics.totalLegCnt,
          value2.away.statistics.goalCnt,
          value2.away.statistics.totalLegCnt);
      } else {
        timeNotify(data2.errorMessage);
      }

    } else {
      timeNotify(data.errorMessage);
    }
  } catch (error) {
    // 处理错误
    console.error(error);
  }
}

// 关闭弹出框
const close = () => {
  isVisible.value = false;
}

// 暴露给外部使用的函数
defineExpose({
  show,
  close
});

</script>

<style>
.fontCoarse {
  font-weight: bold;
}
.title {
  line-height: 30px !important;
  border: 0px !important;
}
.content {
  line-height: 45px;
  text-align: center;
  border: 1px solid var(--nut-table-border-color, #ececec);
  vertical-align: middle;
}
.left {
  line-height: 45px;
  text-align: left;
  border: 1px solid var(--nut-table-border-color, #ececec);
  padding-left: var(--nut-table-cols-padding, 5px);
}
</style>